.sidemenu-component {
  height: 100%;
  width: 200px;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  overflow-x: hidden;
  padding-top: 20px;
}

.sidemenu-component a {
  padding: 6px 6px 6px 32px;
  text-decoration: none;
  font-size: 16px;
  color: black;
  display: block;
}
